<template>
  <div class="r-editor">
    <quill-editor
      v-model="content"
      ref="myQuillEditor"
      :options="editorOption"
      @blur="onEditorBlur($event)"
      @focus="onEditorFocus($event)"
      @ready="onEditorReady($event)"
    ></quill-editor>

    <!-- <van-action-sheet v-model="show" title>
      <div class="footer">
        <div class="pic" @click="selectPhoto(1)">
          <div class="icon">
            <i class="iconfont icontupian"></i>
          </div>
          <div class="text">相册</div>
        </div>
        <div class="line"></div>
        <div class="pho" @click="selectPhoto(2)">
          <div class="icon">
            <i class="iconfont iconphotograph"></i>
          </div>
          <div class="text">拍照</div>
        </div>
      </div>
    </van-action-sheet>-->
  </div>
</template>

<script>
import VueQuillEditor from "vue-quill-editor";
import { ActionSheet } from "vant";
// import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
// import "quill/dist/quill.bubble.css";
import { ModulePicture } from "@/utils/enum";
import { FuncType, sendToAPP } from "@/utils/interFlow";
export default {
  name: "RQuillEditor",
  components: {
    "quill-editor": VueQuillEditor.quillEditor,
    [ActionSheet.name]: ActionSheet
  },
  data() {
    return {
      // show: false,
      fileId: "",
      imgBase: "",

      content: `<h1 class="ql-align-center">
                  <span class="ql-font-serif"><span class="ql-cursor"></span>I am Example 3!</span></span>
                  </h1>
                  <p><br></p>
                  <p><strong class="ql-font-serif">Whenever you play the game of thrones, you either win or die. There is no middle ground.</strong></p>
                  <p><br></p>
                  <p><u class="ql-font-serif">Some war against sword and spear to win, and the others the crow and the paper to win.</u></p>
                  <p><br></p>
                  <p><em class="ql-font-serif">Dead history is write in ink, the living sort in blood.</em></p>
                  <p><br></p>
                  <p><span class="ql-font-serif" style="color: rgb(0, 102, 204);">They're only numbers. Numbers on paper. Once you understand that, it's easy to make them behave.</span></p>
                  <p><br></p>
                  <p><span class="ql-font-serif">Every time we deal with an enemy, we create two more.</span></p>
                  <p><br></p>
                  <p><span class="ql-font-serif">So the king has decreed. The small council consents.</span></p>
                  <p><br></p>
                  <p><span class="ql-font-serif">Chaos not is a pit, chaos is a ladder.</span></p>
                  <p><br></p>
                  <p><span class="ql-font-serif">A chain needs all sorts of metals, and a land needs all sorts of people.</span></p>
                  <p><br></p>
                  <p><span class="ql-font-serif">When the snows fall and the white winds blow, the lone wolf dies, but the pack survives.</p>
                  `,
      editorOption: {
        placeholder: "请输入文字",
        theme: "snow", // or 'bubble'
        modules: {
          toolbar: {
            container: [
              ["bold", "italic", "underline"], // 加粗 斜体 下划线
              [{ color: [] }], // 字体颜色
              [{ header: [1, 2, 3, 4, 5, 6, false] }], // 1、2 级标题
              [{ font: [] }], // 字体种类

              ["blockquote", "code-block"], // 引用  代码块
              [{ align: [] }], // 对齐方式
              [{ indent: "-1" }, { indent: "+1" }], // 缩进
              [{ list: "ordered" }, { list: "bullet" }], // 有序、无序列表
              ["image", "photo"] // 链接、图片、视频
            ],
            handlers: {
              image: value => {
                console.log("1111111111", value);
                if (value) {
                  sendToAPP(FuncType.albumOpen, "");
                  // document.querySelector("#quill-upload input").click();
                } else {
                  this.quill.format("image", false);
                }
              },
              photo: value => {
                console.log("22222", value);
                if (value) {
                  sendToAPP(FuncType.cameraOpen, "");
                  // document.querySelector("#quill-upload input").click();
                } else {
                  this.quill.format("photo", false);
                }
              }
            }
          }
        }
      }
    };
  },
  created() {},
  computed: {
    // contentCode() {
    //   return ""; //hljs.highlightAuto(this.content).value
    // },
    // editor() {
    //   return this.$refs.myQuillEditor.quill;
    // }
  },

  mounted() {
    // console.log("this is current quill instance object", this.editor);
    // http://192.168.0.111:50120/GetPicture?fileName=19092017325304695400486099498177&fileModule=1

    // this.$nextTick(() => {
    //   setTimeout(() => {
    //     let quill = this.$refs.myQuillEditor.quill;
    //     let length = quill.getSelection().index;
    //     console.log("length:", length);
    //     let filePath =
    //       "http://192.168.0.111:50120/GetPicture?fileName=19092017300936094701591023748968&fileModule=5";
    //     // 插入图片  res.info为服务器返回的图片地址
    //     quill.insertEmbed(length, "image", filePath);
    //     // 调整光标到最后
    //     quill.setSelection(length + 1);
    //   }, 5000);
    // });
  },
  methods: {
    onEditorBlur(editor) {
      // console.log('editor blur!', editor)
    },
    onEditorFocus(editor) {
      // console.log('editor focus!', editor)
    },
    onEditorReady(editor) {
      // console.log('editor ready!', editor)
    },
    customButtonClick() {
      alert(
        `You can custom the button and listen click event to do something...\n你可以定义一些自定义按钮做你想做的事，如上传图片至第三方存储再插入内容区...等等`
      );
    },
    selectPhoto(type) {
      if (type === 2) {
        sendToAPP(FuncType.cameraOpen, "");
      } else {
        sendToAPP(FuncType.albumOpen, "");
      }
    },
    // 返回布尔值
    beforeRead(file) {
      if (
        file.type !== "image/jpeg" &&
        file.type !== "image/png" &&
        file.type !== "image/jpg"
      ) {
        this.$toast("请上传 jpg 格式图片");
        return false;
      }
      // 超过20M
      if (file.size > 1024 * 1024 * 20) {
        this.$toast("请上传小于20M的图片");
        return false;
      }
      return true;
    },
    afterRead(file) {
      console.log("接收到的文件：", file);
      // 超过20M
      if (file.length > 1024 * 1024 * 20) {
        this.$toast("请上传小于20M的图片");
        return false;
      }
      if (file) {
        let reqData = {
          funType: ModulePicture.StudentNote,
          imgBaseString: file
        };
        this.$gatewayHttp.resourse
          .upload(reqData)
          .then(res => {
            console.log("res:", res);
            if (res.Result) {
              this.imgBase = file;
              this.fileId = res.Data;

              // res为图片服务器返回的数据
              // 获取富文本组件实例
              let quill = this.$refs.myQuillEditor.quill;
              console.log("quill:", quill);
              console.log("fileId:", this.fileId);
              // 如果上传成功
              if (this.fileId) {
                // 获取光标所在位置
                let length = quill.getSelection().index;
                console.log("length:", length);
                let filePath = this.$gatewayHttp.resourse.picture(
                  this.fileId,
                  ModulePicture.StudentNote
                );
                // 插入图片  res.info为服务器返回的图片地址
                quill.insertEmbed(length, "image", filePath);
                // 调整光标到最后
                quill.setSelection(length + 1);
              } else {
                this.$toast("图片插入失败");
              }
            }
          })
          .catch(err => {
               console.log("err:", err);
          });
      }
    }
  }
};
</script>

<style>
.editor {
  line-height: normal !important;
  height: 800px;
}
.ql-snow .ql-tooltip[data-mode="link"]::before {
  content: "请输入链接地址:";
}
.ql-snow .ql-tooltip.ql-editing a.ql-action::after {
  border-right: 0px;
  content: "保存";
  padding-right: 0px;
}

.ql-snow .ql-tooltip[data-mode="video"]::before {
  content: "请输入视频地址:";
}

.ql-snow .ql-picker.ql-size .ql-picker-label::before,
.ql-snow .ql-picker.ql-size .ql-picker-item::before {
  content: "14px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="small"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="small"]::before {
  content: "10px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="large"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="large"]::before {
  content: "18px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="huge"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="huge"]::before {
  content: "32px";
}

.ql-snow .ql-picker.ql-header .ql-picker-label::before,
.ql-snow .ql-picker.ql-header .ql-picker-item::before {
  content: "文本";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="1"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="1"]::before {
  content: "标题1";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="2"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="2"]::before {
  content: "标题2";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="3"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="3"]::before {
  content: "标题3";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="4"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="4"]::before {
  content: "标题4";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="5"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="5"]::before {
  content: "标题5";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="6"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="6"]::before {
  content: "标题6";
}

.ql-snow .ql-picker.ql-font .ql-picker-label::before,
.ql-snow .ql-picker.ql-font .ql-picker-item::before {
  content: "标准字体";
}
.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="serif"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="serif"]::before {
  content: "衬线字体";
}
.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="monospace"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="monospace"]::before {
  content: "等宽字体";
}

/*拍照样式*/
.ql-photo {
  font-family: "iconfont" !important;
  font-size: 16px;
  line-height: 18px;
  font-style: normal;
  font-weight: bolder;
  -webkit-font-smoothing: antialiased;
}
.ql-photo::before {
  content: "\E641";
}
</style>

<style lang="less" scoped>
.quill-editor,
.quill-code {
  width: 100vw;
  // float: left;
}
.quill-code {
  height: auto;
  border: none;
  > .title {
    border: 1px solid #ccc;
    border-left: none;
    height: 3em;
    line-height: 3em;
    text-indent: 1rem;
    font-weight: bold;
  }
  > code {
    width: 100%;
    margin: 0;
    padding: 1rem;
    border: 1px solid #ccc;
    border-top: none;
    border-left: none;
    border-radius: 0;
    height: 30rem;
    overflow-y: auto;
  }
}

.r-editor {
  /deep/ .ql-editor {
    max-height: 45vh;
  }
}

